import CcButton from '@/components/cc-button/cc-button.vue';
export default {
	data() {
		return {
			title: 'Hello',
			isloading: false
		};
	},
	components: {
		CcButton
	},
	onLoad() {},
	methods: {
		showloading() {
			this.isloading=true
			setTimeout(() => {
				this.isloading=false
			}, 3500);
		}
	}
};
