export default {
	data() {
		return {};
	},
	props: {
		loading: {
			type: Boolean,
			default: false
		},
		color:{
			type:String,
			default:'#ffffff'
		},
		bgcolor:{
			type:String,
			default:'#333333'
		},
		width:{
			type:String,
			default:'100rpx'
		},
		height:{
			type:String,
			default:'80rpx'
		}
	}
};
